@import '../variables.scss';
@import '../mixins.scss';

.main {
  &__wrap {
    max-width: $desktop-width;
    margin: 0 auto;
  }

  &__title,
  &__title_questions {
    font-size: 21.242px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
    color: #ffffff;
    padding: 14px;
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
      rgba(162, 162, 162, 0.6);
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
      0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
      0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
      0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
      0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
    border-radius: 57.3534px;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      width: 29px;
      height: 22px;
      top: 16px;
      left: 115px;
      background-image: url('../../img/user-list.svg');
      background-repeat: no-repeat;
    }
  }

  &__title_questions::before {
    left: 142px;
    background-image: url('../../img/question-list.svg');
  }

  &__users {
    position: absolute;
    top: 365px;
    left: calc(50% - 600px);
    width: 590px;
    z-index: 1;
  }

  &__questions {
    position: absolute;
    top: 365px;
    right: calc(50% - 600px);
    width: 590px;
    z-index: 1;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 30px;
    line-height: 1.4;
    padding: 20px 0;
  }

  &__user,
  &__question {
    background: #ffffff;
    border-radius: 40px;
    font-size: 40px;
    font-weight: 500;
    padding: 16px 45px;
    color: #326799;
  }

  &__user {
    margin-bottom: 30px;
  }

  &__question {
    margin-bottom: 50px;
  }
}
