@import './normalize.scss';

@font-face {
	font-family: 'Montserrat';
	src: local('Montserrat Regular'),
		url('../fonts/Montserrat-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Montserrat';
	src: local('Montserrat Medium'),
		url('../fonts/Montserrat-Medium.woff') format('woff');
	font-weight: 500;
	font-style: normal;
	font-display: swap;
}

html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Geneva', sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  color: #000000;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  background: linear-gradient(180deg, #A0BEC8 0%, rgba(255, 255, 255, 0) 225.45%), #A0BEC8;
  background-image: url("../img/b-i-big-min.jpg");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
}