@import '../variables.scss';
@import '../mixins.scss';

.header {
  
  &__title {
    font-weight: 500;
    font-size: 72px;
    line-height: 88px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__wrap {
    padding: 20px 10px;
    max-width: $desktop-width;
    margin: 0 auto;
  }

  &__list {
    @include list-reset;
    display: flex;
    position: relative;
    justify-content: space-between;
  }

  button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 158px;
    height: 160px;
    border: none;
    border-radius: 46px;
    outline: none;
    color: #6c7395;
    text-transform: uppercase;
    background-color: #6c7395;
    background-image: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
    transition: 0.3s;

    img {
      margin-bottom: 28px;
    }
    span {
      display: block;
      width: 135px;
    }
  }

  &__item:first-child button img,
  &__item:last-child button img {
    margin-bottom: 37px;
  }

  &__item:last-child {
    position: absolute;
    right: 0;
    top: 170px;
  }

  button:focus,
  button:hover {
    box-shadow: 0px 0px 3px 3px rgba(34, 60, 80, 0.2);
  }

  button:active {
    opacity: 0.7;
  }

  &__time {
    font-size: 30px;
    text-transform: uppercase;
    border-radius: 46px;
    padding: 15px;
    outline: none;
    border: none;
    background-color: inherit;
    box-shadow: 0px 0px 3px 3px rgba(34, 60, 80, 0.2);
    color: #ff9e9e;

    &:focus {
      box-shadow: 0px 0px 3px 3px rgba(221, 255, 194, 0.5);
    }
  }
}
