@import '../variables.scss';
@import '../mixins.scss';

.timer {
  font-weight: 500;
  width: 306px;
  height: 279px;
  @include flex-mobile;
  padding-top: 40px;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
    rgba(162, 162, 162, 0.6);
  border-radius: 32.0725px;

  &__time {
    font-size: 54px;
    text-transform: uppercase;
    color: #ff9e9e;
  }

  &__text {
    font-size: 15px;
    text-transform: uppercase;
    color: #ddffc2;
    margin-bottom: 13px;
  }

  button {
    border: none;
    outline: none;
    background-color: inherit;
  }

  button:focus,
  button:hover {
    box-shadow: 0px 0px 3px 3px rgba(34, 60, 80, 0.2);
  }

  button:active {
    opacity: 0.7;
  }

  button:disabled {
    opacity: 0.5;
  }

  &__button_wrap {
    margin-bottom: 15px;

    button {
      padding-top: 7px;
    }

    button:first-child {
      margin-right: 24px;
    }
  }

  &__button_restart {
    width: 157.03px;
    font-size: 15px;
    text-transform: uppercase;
    color: #968080;
    padding: 7px;
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
      rgba(162, 162, 162, 0.6);
    box-shadow: 0px 75.4932px 60.3946px rgba(0, 0, 0, 0.07),
      0px 44.4504px 31.5018px rgba(0, 0, 0, 0.050624),
      0px 22.8291px 14.7846px rgba(0, 0, 0, 0.039312),
      0px 9.17997px 6.4743px rgba(0, 0, 0, 0.030688),
      0px 2.05341px 2.80231px rgba(0, 0, 0, 0.019376);
    border-radius: 43px;
  }
}
